// reducer
export default function msgReducer(state = initState, action) {
  console.log('call msg reducer');
  switch (action.type) {
    case SHOW_MSG:
      return {
        ...state,
        isActive: true,
        isSuccess: action.result,
        type: action.msgType,
      }

    case HIDE_MSG:
      return {
        ...state,
        isActive: false,
      }

    default:
      return state
  }
}

// action creators
export const showMsg = (msgType, result) => {
  return {
    type: SHOW_MSG,
    result,
    msgType,
  }
}

export const hideMsg = () => {
  return {
    type: HIDE_MSG,
  }
}
